//form

const formScrollTrigger = new ST( {
	trigger: '.form-subscription',
	start: '-40%',
	once: true,
	onEnter: () => {
		formSectionTimeline.play();
	}
} );

const formSectionTimeline = g.timeline( {
	repeat: 0,
	scrollTrigger: formScrollTrigger
} );

const formTitleElement = document.querySelector( '.form-subscription__left-title div' );
const formLines = formTitleElement.innerHTML.split( '<br>' );
const formCleanLines = formLines
	.map( line => line.trim() )
	.filter( line => line !== '' )
	.map( line => line.replace( /amp;/g, '' ) );

formTitleElement.textContent = '';

formCleanLines.forEach( ( l, i ) => {

	const textWrapper = document.createElement( 'div' );
	const textElement = document.createElement( 'div' );

	textWrapper.style.overflow = 'hidden';

	textElement.textContent = l;
	textWrapper.appendChild( textElement );
	formTitleElement.appendChild( textWrapper );

	formSectionTimeline.from( textElement, {
		duration: 1,
		opacity: 0,
		y: '100%',
		ease: 'power2.out'
	}, 0.2 * i );

} );

[ '.form-subscription__right-title', '.form-subscription__left-box svg', ...Array.from( $( '.form-subscription__right-form' ).children() ) ]
	.forEach( ( item, index ) => {
		formSectionTimeline.from( item, {
			position: 'relative',
			y: index === 1 ? 0 : '90%',
			duration: 1,
			opacity: 0,
			ease: 'power2.out'
		}, 0.5 * index );
	} );

//footer

const footerScrollTrigger = new ST( {
	trigger: '.footer',
	start: '-60%',
	once: true,
	onEnter: () => {
		footerSectionTimeline.play();
	}
} );

const footerSectionTimeline = g.timeline( {
	repeat: 0,
	scrollTrigger: footerScrollTrigger
} );

footerSectionTimeline.from( '.footer__logo div', {
	duration: 1,
	opacity: 0,
	y: '100%',
	ease: 'power2.out'
}, 0.2 );

footerSectionTimeline.to( '.footer__logo-line-linehider', {
	duration: 1,
	width: 0,
	ease: 'power2.out'
}, 0.4 );

[ '.footer__box-shop', '.footer__box-information', '.footer__box-item', '.footer__undertext', '.footer__box-first-design' ]
	.forEach( ( item, index ) => {
		footerSectionTimeline.from( item, {
			duration: 1,
			y: '100%',
			opacity: 0,
			ease: 'power2.out'
		}, index < 3 ? 0.1 * index : 0.4 * index );
	} );


